.moduleHeader {
  margin: 30px auto 10px;

  .title {
    position: relative;
    font-weight: 300;
    color: #fff;
    text-transform: capitalize;
    font-size: 36px;
    font-family: 'Josefin Sans', sans-serif;
    line-height: 1.2;
    letter-spacing: 5px;
    @media (max-width: 837px) {
      margin: 20px auto 10px;
    }
  }

  .moduleLine {
    background: #4e93e6;
    width: 55px;
    height: 1px;
    margin: 10px auto;
  }
}







